import { isTagEmbed, isTagShop } from "../../../../actions/api";
import MediaIcon from "../../Elements/MediaIcon/MediaIcon";
import Image from "../../Elements/Media/Image";
import Video from "../../Elements/Media/Video";
const Media = ({ itemData, wallID, ownerId, wall }) => {
  const autoPlayTagEmbed = isTagEmbed && (itemData.type === 3 || itemData.type === 5) && !wall.Personalization.mobilePopup && !wall.Personalization.postFeatured ? true : false
  const autoPlay = (itemData.type === 3 || itemData.type === 5) && wall.ThemeRule.autoPlay == 1 ? true : false
  const size = isTagEmbed ? wall.ThemeRule.aspectImageRatio ? wall.ThemeRule.aspectImageRatio : 80 : isTagShop ? false : 100
  const mediaIcon = isTagShop && itemData.hotspot ? false : true

  return <div className="tb_cp_media_wrap">
    {!autoPlayTagEmbed ? <MediaIcon itemData={itemData} IconClass="tb_cp_media_icon" show={mediaIcon} isCenter={isTagEmbed} /> : null}
    {autoPlay || autoPlayTagEmbed ?
      <Video VideoClass="tb_cp_video" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={size} isCover={true} controls={true} autoPlay={false} muted={true} />
      :
      <Image ImageClass="tb_cp_image" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={size} ThemeRule={wall.ThemeRule} hotspot={true} />
    }
  </div>
}
export default Media