import React, { PureComponent } from "react";
import Card from "./Elements/Card";
import { connect } from 'react-redux';
import Masonry from 'react-masonry-component';
import { managePostHeight } from '../../../actions/themeActions';
import { isTagShop } from '../../../actions/api'
import "./scss/ClassicPhoto.scss";


const ClassicPhotoExtend = ({ loadData, postData, completeDataObject, BrandingPosition, clickToShowPopUp, wall, adjustWidth, onClickToCTA }) => {
  return loadData ?
    ((postData && postData.length > 0)) && postData.map((item, index) => {
      const cardData = completeDataObject[item];
      const IsBrandingCard = (BrandingPosition === index) ? false : false
      IsBrandingCard ? BrandingPosition += parseInt(index) + 10 : parseInt(BrandingPosition)
      return <Card ownerId={wall.Wall.owner} key={`Classic_Card_${index}_${item.id}`} itemData={cardData} itemIndex={index} adjustWidth={adjustWidth} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} wallID={wall.Wall.id} clickToShowPopUp={clickToShowPopUp} onClickToCTA={onClickToCTA} wall={wall} />
    }) : null
}


class ClassicPhoto extends PureComponent {
  state = {
    postData: [],
    loadData: 1
  }
  componentDidMount() {
    const { postData } = this.props;
    this.setState({ postData, loadData: 1 })
  }

  render() {
    const { postData, completeDataObject, adjustWidth, wall, clickToShowPopUp, onClickToCTA } = this.props;
    const { loadData } = this.state;
    var BrandingPosition = 4

    return isTagShop ? <Masonry
      className={`tb_cp_post_container`}
      tabIndex='0'
      aria-label={`There are ${(postData && postData.length > 0) ? postData.length : 0} posts in the feed`} role="feed"
      elementType={'div'}
      options={{
        transitionDuration: 0,
        percentPosition: true,
        itemSelector: '.tb_cp_post_wrapper',
        horizontalOrder: true,
        resize: true,
      }}
      disableImagesLoaded={false}
      updateOnEachImageLoad={false}
      imagesLoadedOptions={{}}
      enableResizableChildren={true}>
      <ClassicPhotoExtend loadData={loadData} wall={wall} clickToShowPopUp={clickToShowPopUp} postData={postData} completeDataObject={completeDataObject} BrandingPosition={BrandingPosition} adjustWidth={adjustWidth} onClickToCTA={onClickToCTA} />
    </Masonry> : <div className="tb_cp_post_container"><ClassicPhotoExtend loadData={loadData} wall={wall} clickToShowPopUp={clickToShowPopUp} postData={postData} completeDataObject={completeDataObject} BrandingPosition={BrandingPosition} adjustWidth={adjustWidth} onClickToCTA={onClickToCTA} /></div>
  }
}

const mapStateToProps = state => {
  return {}
}
const mapDispatchToProps = dispatch => {
  return {
    managePostHeight: (data) => dispatch(managePostHeight(data))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(ClassicPhoto)